.main-page {
  // .main-page__promo
  &__promo {
    margin-top : 85px;
  }
}

.promo-blocks {
  margin-left  : -5px;
  margin-right : -5px;
  display      : flex;


  // .promo-blocks__item
  &__item {
    margin     : 0 5px;
    flex       : 1 1 calc(100% / 3 - 20px);
    min-height : 160px;
  }
}

.promo-block {
  background-repeat : no-repeat;
  background-size   : cover;
  display           : inline-block;
  padding           : 15px 23px 75px;
  line-height       : 1.3;


  // .promo-block--special
  &--special {
    background-image : url(../../img/pages/index/promo/1.png);
  }

  // .promo-block--popular
  &--popular {
    background-image : url(../../img/pages/index/promo/2.png);
  }

  // .promo-block--actions
  &--actions {
    background-image : url(../../img/pages/index/promo/3.png);
  }

  // .promo-block__title
  &__title {

    font-size      : 24px;
    color          : #fff;
    font-weight    : bold;
    text-transform : uppercase;

  }
}
